import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/contact",
    name: "contact",
    component: () => import("../views/Contact.vue"),
  },

  {
    path: "/about-us",
    name: "aboutUs",
    component: () => import("../views/AboutUs.vue"),
  },
  {
    path: "/terms-conditions",
    name: "terms",
    component: () => import("../views/TermsConditions.vue"),
  },
  {
    path: "/privacy-policy",
    name: "privacyPolicy",
    component: () => import("../views/PrivacyPolicy.vue"),
  },
  {
    path: "/service-detail",
    name: "serviceDetail",
    props: true,
    component: () => import("../views/home-sections/ServiceDetail.vue"),
  },
  {
    path: "/all-services",
    name: "allServices",
    props: true,
    component: () => import("../views/AllServices.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
});

export default router;
