<script>
export default {
  data: () => ({
    activeClass: "my-active",
  }),

  computed: {
    currentPage() {
      return this.$route.name;
    },
  },

  methods: {
    checkCurrentActiveNavItem(element) {
      var linkArray = [];
      switch (element) {
        case 1: // Home
          linkArray = ["home"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 2: // About
          linkArray = ["aboutUs"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 3: // Contact
          linkArray = ["contact"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 4: // Terms & conditions
          linkArray = ["terms"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 5: // Privacy Policy
          linkArray = ["privacyPolicy"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
      }
    },
  },
};
</script>

