<template>
  <div>
    <div class="my-header">
      <v-container class="pa-0">
        <v-row>
          <v-col class="cols-12 col-sm-6 col-md-6 ">
            <router-link :to="{ name: 'home' }">
              <img src="../../assets/logo.png" alt=""
            /></router-link>
          </v-col>

          <v-col class="cols-12 col-sm-6 col-md-6 top-nav-flex">
            <ul class="banner-links">
              <li>
                <div :class="[checkCurrentActiveNavItem(1) ? activeClass : '']">
                  <router-link :to="{ name: 'home' }">Home</router-link>
                </div>
              </li>

              <li>
                <div :class="[checkCurrentActiveNavItem(2) ? activeClass : '']">
                  <router-link :to="{ name: 'aboutUs' }">About Us</router-link>
                </div>
              </li>

              <li>
                <div :class="[checkCurrentActiveNavItem(4) ? activeClass : '']">
                  <router-link :to="{ name: 'contact' }">Contact</router-link>
                </div>
              </li>
            </ul>
            <v-app-bar-nav-icon
              class="nav-icon"
              @click="expand = !expand"
            ></v-app-bar-nav-icon>
            <v-btn rounded depressed dark large color="primary"
              ><v-icon left>mdi-phone</v-icon> Call Now
            </v-btn>
          </v-col>
        </v-row>
      </v-container>

      <v-expand-transition>
        <v-card flat v-show="expand">
          <v-list nav dense>
            <v-list-item-group>
              <v-list-item @click="home()">
                <v-list-item-title>Home</v-list-item-title>
              </v-list-item>

              <v-list-item @click="aboutUs()">
                <v-list-item-title>About Us</v-list-item-title>
              </v-list-item>

              <v-list-item @click="contact()">
                <v-list-item-title>Contact</v-list-item-title>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-expand-transition>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    activeClass: "my-active",
    expand: false,
    drawer: false,
  }),

  computed: {
    currentPage() {
      return this.$route.name;
    },
  },

  methods: {
    home() {
      this.expand = false;
      this.$router.push({ name: "home" });
    },

    aboutUs() {
      this.expand = false;
      this.$router.push({ name: "aboutUs" });
    },

    contact() {
      this.expand = false;
      this.$router.push({ name: "contact" });
    },

    checkCurrentActiveNavItem(element) {
      var linkArray = [];
      switch (element) {
        case 1: // Home
          linkArray = ["home"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 2: // About
          linkArray = ["aboutUs"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 3: // Services
          linkArray = ["services"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
        case 4: // Contact
          linkArray = ["contact"];
          if (linkArray.includes(this.currentPage)) return true;
          break;
      }
    },
  },
};
</script>

<style>
.my-active a {
  background-color: #0dcb0712;
  border-radius: 14px;
  font-weight: 600;
  color: var(--primary) !important;
}
</style>
